export default {
	props: {
		dataSource: {
			type: Object,
			default: {}
		}
	},
	methods: {
		upgradeHandle(e) {
			this.$emit('upgradeHandle', e)
		},
		detailHandle(e) {
			this.$emit('detailHandle', e)
		},
		completeHandle(e) {
			this.$emit('completeHandle', e)
		},
		callHandle() {
			uni.makePhoneCall({
			    phoneNumber: this.dataSource.tell //仅为示例
			});
		}
	}
}
